div,h1,h2,h3,p,header,footer,main,button,ul,li,a{
    margin: 0; padding: 0;
}
body{
    overflow-x:hidden;
}
body::-webkit-scrollbar{
    display: none;
}
button{
    border: 0px none;
    background-color:transparent;
}
img{
    border: 0px none;
}
a{
    text-decoration: none;
}
ul{
    list-style: none;
}
address{
    font-style: normal;
}
@mixin flexB{
    display: flex; justify-content:space-between; align-items:center;
}
@mixin flexC{
    display: flex; justify-content:center; align-items: center;
}
.App{
    width:100vw; height: 100%;
    position: relative;
    color: #090909;
    overflow-x: hidden;
    .header{
        position:fixed; width: 100vw; height: 50px;
        left: 0; top: 0; background-color: #f30;
        @include flexB; z-index: 1000; 
        h1{
            width: 150px; height: 40px; margin-left: 15px;
        }
        button{
            margin-right: 15px; width: 25px; height: 20px;
            @include flexB;
            flex-flow:column nowrap; position: relative; z-index: 10000;
            &.on{
                .line:nth-child(1){
                    transform:translateY(8px) rotate(45deg);
                }
                .line:nth-child(2){
                    opacity: 0;
                }
                .line:nth-child(3){
                    transform:translateY(-8px) rotate(-45deg);
                }
            }
            .line{
                display: block; width: 100%; height: 4px;
                background-color:#090909; transition:all .3s;
            }
        }
        .nav{
            position: absolute; top:50px; right: -320px;
            width: 318px; height: 371px; background-color: #fff;
            z-index:5000; transition:all .3s; overflow: hidden;
            border-radius:0 0 0 30px; visibility:hidden;
            border-left: 2px solid #666; border-bottom: 2px solid #666; 
            &.on{
                right: 0; visibility:visible;
            }
            ul{
                width: 100%;
                li{
                    border-bottom: 1px solid #333;
                    a{
                        display: block;
                        padding:20px 40px;
                        color: #333; font-weight: bold;
                    }
                }
            }
        }
    }
    .footer{
        position:fixed; width: 100vw; height: 50px;
        left: 0; bottom: 0; background-color: #fff;
        @include flexB; z-index: 2000;
        button{
            @include flexC;
            flex-flow:column nowrap; width: 66px;
            .block{
                display: block;
            }
        }
        .left{
            margin-left: 15px;
        }
        .right{
            margin-right: 15px;
        }
    }
    .main{
        width: 100%; height:100%;
        box-sizing: border-box;
        padding: 50px 0px;
        .main_banner{
            width: 100%; height: 400px;
            background:url('./assets/main.jpg') no-repeat;
            background-size:cover; text-align: center;
            .text{
                margin-left: 45px;
                padding-top: 60px;
                h2{
                    color: #fff; font-size: 32px;
                    width: 160px; margin-bottom: 20px;
                }
                p{
                    color: #fff; font-size: 16px;
                    text-align: left;
                }
            }
            a{
                display: inline-block; margin: 30px auto 0px; color: #090909; border-radius: 15px;
                padding: 10px 20px; text-align: center; background-color: #fc0;
                font-size: 18px; font-weight: bold;
            }
        }
        .menus{
            width: 90%; height:180px;
            display:grid; grid-template-columns:repeat(3,1fr);
            grid-template-rows:repeat(2,1fr); border-radius:20px 0 0 0;
            margin: -100px auto 0px; overflow: hidden;
            border-right: 1px solid #666; border-bottom: 1px solid #666;
            .btns{
                background-color: #fff; @include flexC;
                flex-flow:column nowrap; 
                text-align: center; font-size: 14px;
                gap: 10px; border-left: 1px solid #666; border-top: 1px solid #666;
            }
        }
    }
    .events{
        width: 100%; height: 200px;
        margin:40px 0px; background-color: #2c5;
        .event{
            width: 100%; height: 200px;
            background:url('./assets/review.jpg') no-repeat center center;
            background-size:cover;
            p{
                color: #fff; font-weight: bold; font-size: 24px;
                text-shadow:2px 2px 4px rgb(0,0,0,0.8);
                margin-left: 20px; margin-top: 20px;
                .emp{
                    display: block; margin-top: 10px; color: #0cf;
                    font-size: 36px;
                }
            }
        }
        .event2{
            width: 100%; height: 200px;
            background:url('./assets/event2.jpg') no-repeat center center;
            background-size:cover;
            p{
                color: #fff; font-weight: bold; font-size: 24px;
                text-shadow:0px 2px 2px rgb(0,0,0,0.2);
                margin-left: 20px; margin-top: 110px;
                .emp2{
                    color: #f20;
                    font-size: 36px;
                }
            }
        }
    }
    .info{
        .info_top{
            width: 100%; background-color: #ededed;
            padding:20px 0px;
            ul{
                @include flexC;
                li{
                    margin-left:10px;
                    padding-left: 10px;
                    a{
                        color: #222;
                        font-size: 14px;
                    }
                    .important{
                        font-weight: bold;
                    }
                }
                li:nth-child(2){
                    border-left: 1px solid #888;
                }
            }
            div{
                @include flexC;
                flex-flow:column nowrap;
                margin-top: 10px;
                gap: 10px;
                a{
                    font-size: 14px; color: #222;
                    display:inline-block; text-align: left;
                }
            }
        }
        .info_bottom{
            width: 100%; padding: 20px 15px;
            box-sizing: border-box;
            background-color: #bbb;
            color: #090909;
            display: flex; justify-content:center;
            align-items:flex-start;
            flex-flow:column nowrap;
            gap: 10px;
            address{
                font-size: 14px;
            }
            p{
                font-size: 14px;
            }
        }
    }
    .roulette{
        padding:50px 0px; width: 100%; height: 100vh;
        min-height:640px;
        background-color: #0a0a5c; box-sizing: border-box;
        position: relative;
        display: flex;
        justify-content:center;
        align-items:center;
        flex-flow:column nowrap;
        h2{
            text-align: center; margin-top: 10px; margin-bottom: 20px;
            color: #fff;
            span{
                color: #f20;
            }
        }
        .container{
            width:320px; height: 320px;
            position: relative;
            div{
                width:320px; height: 320px;
            }
            button{
                width: 25%; height: 25%; background-color: #2b2;
                border-radius: 50%; position: absolute; left: 50%; top: 50%;
                transform:translate(-50%,-50%);
                color: #fff; font-size: 18px;
                box-sizing: border-box; border: 5px solid #fc0;
                &.off{
                    background-color: #ccc;
                }
            }
        }
        p{
            color: #fff; font-weight: bold;
        }
        .detail{
            margin-bottom: 10px; text-align: center;
        }
        .caution{
            margin-top: 20px; font-size: 14px; 
            padding-left: 10px; padding-bottom: 10px;
        }
        .congrat{
            position: fixed; left: 50%; top: 50%; box-sizing: border-box;
            transform:translate(-50%,-50%); background-color: #fff;
            width: 90%; height: 320px; z-index: 10000; padding:10px;
            border-radius: 20px; display: none; text-align: center;
            &.show{
                display: block;
            }
            h3{
                text-align: center; font-size: 28px; margin-bottom: 40px;
                color: #02f;
            }
            p{
                color: #090909;
                text-align: center;
                strong{
                    font-size: 28px;
                    span{
                        font-size: 32px; display: inline-block;
                        padding-bottom: 20px; color: #f30;
                    }
                }
                &:nth-of-type(2){
                    margin:20px 0px;
                }
            }
            button{
                background-color: #ccc; padding:10px 20px; border-radius: 20px;
                font-weight: bold; font-size: 16px;
            }
        }
        .bg{
            position: fixed; left: 0; top: 0; width: 100%; height: 100%;
            background-color:rgba(0,0,0,0.9); z-index: 9000; display: none;
            &.show{
                display: block;
            }
        }
    }
    .bold{
        font-weight: bold;
    }
}